.container {
    padding: 10px;
    min-height: 400px;
    width: 160px;
    margin-right: 20px;
}
.label {
    font-size: 20px;
}
.form {
    min-height: 380px;
    position: relative;
}
.listOfFilter{
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.buttonFilter, .buttonReset{
    font-size: 16px;
    position: absolute;
    bottom: 0;
    width: 100%;
    right: 0;
}
.buttonReset {
    margin-bottom: 5px;
    bottom: 30px;
}