.container {
    color: white;
    min-height: 460px;
    font-size: 18px;
    display: flex;
    justify-content: space-around;
    position: relative;
    background: url('../../../UI/images/promo-wheel.jpg') center repeat ;
    background-position: 0% 50%;
    background-size: cover;
    position: relative;
}
.promoContainer {
    position: absolute;
    top: 25px;   
    right: 100px;
    font-size: 20px;
}
.personalData {
    position: absolute;
    bottom: 10px;
}
.form{
    position: absolute;
    top: 40px;
    left: 40px;
    color: black;
    width: 240px;
    min-height: 350px;
    padding: 10px 0;
    margin: auto 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: white;
    border-radius: 10px;
}
.form label{
    margin-bottom: 15px;
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.form input{
    padding-left: 10px;
    border-radius: 5px;
    width: 190px;
    margin: 0 auto;
}
